.formules {
	background-color: var(--secondary-color-faded);
	position: relative;
	margin-top: 60px;
	&::before,
	&::after {
		content: '';
		background-color: var(--secondary-color-faded);
		position: absolute;
		width: 100%;
		height: 50px;
		top: -50px;
		left: 0;
		-webkit-clip-path: polygon(0 0, 0 100%, 100% 100%);
		clip-path: polygon(0 0, 0 100%, 100% 100%);
	}
	&::after {
		top: initial;
		bottom: -50px;
		-webkit-clip-path: polygon(0 0, 0 100%, 100% 0);
		clip-path: polygon(0 0, 0 100%, 100% 0);
	}
	.section__title {
		color: var(--secondary-color-base);
		&::after {
			background-color: var(--secondary-color-base);
		}
	}
	.flex__formules {
		margin-top: 60px;
		display: flex;
		flex-direction: column;
		row-gap: 40px;
		@include mediaSM {
			flex-direction: row;
			gap: 45px;
			flex-wrap: wrap;
			margin-top: 75px;
		}
		@include mediaMD {
			gap: 40px;
			margin-top: 95px;
		}
		@include mediaLG {
			column-gap: 60px;
		}
		.formule {
			width: 303px;
			height: 300px;
			margin-inline: auto;
			border-radius: 50px;
			box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
			background-color: white;
			padding: 30px 15px;
			@include mediaMD {
				flex: 1;
			}
			@include mediaLG {
				padding-inline: 20px;
			}
			&__icon {
				display: block;
				margin-inline: auto;
			}
			&__icon path {
				stroke: var(--secondary-color-base);
			}
			&__title {
				margin-block: 20px;
				font-size: 24px;
				font-family: var(--body-font);
				color: var(--secondary-color-base);
				font-weight: var(--font-semi-bold);
				letter-spacing: 0.05em;
				margin-left: 20px;
				text-transform: capitalize;
				transition: 300ms ease-in;
			}
			&__paragraph {
				font-family: var(--paragraph-font);
				letter-spacing: 0.02em;
				line-height: 27px;
				font-size: 1.125rem;
				margin-left: 20px;
				transition: 300ms ease-in;
			}
		}
	}
}

.formules__btn {
	margin-top: 40px;
	margin-inline: auto;
	background-color: var(--secondary-color-base);
	border: 2px solid var(--secondary-color-base);
	&:hover {
		background-color: var(--secondary-color-dark);
	}
	@include mediaSM {
		margin-top: 60px;
	}
	i {
		margin-left: 5px;
		@include mediaSM {
			margin-left: 10px;
		}
	}
}
